.menu-page {
  // header
  .header {
    padding:#{$padding} 0;

    h1 {
      margin-top: 35px;
      font-size:30px;
      letter-spacing: 25px;
      line-height: 30px;
    }

    img {
      max-width:250px;
    }

    p {
      position:relative;
      top:50%;
      transform:translateY(-50%)
    }
  }

  // menu
  .menu .layout .row {
    margin: 30px auto;

    .img img {
      max-width:80px
    }

    .txt {
      margin:0;
      max-width:100%;
    }
  }
}

@media (max-width:991px) {
  .menu-page .header h1 {
    transform: none;
    padding-left:30px;
    margin-bottom:30px;
  }
  
}