.home {
  // header
  .header {
    height:calc(100vh - #{$navHeight});
    max-height:700px;
    margin-bottom:$margin;

    .row {
      height:calc(100vh - #{$navHeight});
      max-height:700px;
      align-items: center;
    }
    h1 {
      font-size:30px;
      letter-spacing: 25px;
      line-height: 50px;
    }
    p {
      max-width:350px;
      color:$mainColor;
      font-size: 16px;
      margin:auto;
      line-height: 30px;

    }
    a {
      background-color:$yellow;
      color: $mainColor;
      width:200px;
      height:60px;
      border-radius:15px;
      line-height: 60px;
      display:inline-block;
      margin:auto;
      font-size:25px;
      margin-top:30px;
      transition:all .5s ease;
      &:hover {
        background-color:$sub2Color;
      }
    }
  }

  // features 
  .feats {
    background-color:$sub2Color;
    .box {
      padding:#{$padding} 0;
      transition:all .5s linear;
      cursor: pointer;
      &:hover {
        box-shadow: 0 0px 50px #{darken($sub2Color, 5%)};
      }
      .img {
        width:50px;
        margin:0 auto 10px auto;
      }
      p {
        max-width: 250px;
        margin:auto;
      }
    }
  }

  // about
  .about {
    border-bottom:1px solid #{$grey};
    .txt {
      padding:$padding 0;
      p {
        line-height: 2;
      }
    }
  }

  //contact
  .contact {
    .img {
      max-width:400px;
      margin:auto;
      position:relative;
      top:50%;
      transform: translateY(-50%);
      &:before, &:after {
        content:'';
        position:absolute;
        top:130px;
        left:20px;
        background-color:#bd6300;
        width:50px;
        height:80px;
        border-radius: 50%;
        filter:blur(40px);
        z-index: -1;
        animation: smoke 3s ease infinite;
        transform: scale(0);
      }
      &:after {
        animation-delay:1s;
        animation-duration:2s;
        left:10px;
      }
    }
    form {
      max-width:500px;
      margin:0 auto;
      color:$mainColor;
      padding:#{$padding} 0;
      .h1 {
        @include responsiveFont (30,60,320,1550);
        padding-bottom:10px;
        margin-bottom:$margin;
      }
      input,textarea {
        width:100%;
        background-color:$sub2Color;
        border:none;
        border-radius:10px;
        padding:10px;
        outline: none;
      }
      textarea {
        margin-top:$margin / 2;
        height:200px;
        border-radius:10px 10px 0 0px;
      }
      button {
        background-color:$yellow;
        width:100%;
        margin:auto;
        padding:10px 0;
        border-radius:0px 0px 10px 10px;
        border:none;
        margin-top:-3px;
        cursor:pointer;
        color:$txt;
        font-size:20px;
        transition:all .5s ease;
        border:1px solid #{$yellow};
        &:hover {
          background-color:$subColor;
          border-color:$sub2Color
        }
      }
    }
  }

  // location
  .location {
    border-top:1px solid #{$grey};
    padding:#{$padding} 0;
    .img {
      max-width:500px;
      margin:auto;
    }
    .info {
      max-width:400px;
      margin:auto;
      position:relative;
      top:50%;
      transform: translateY(-50%);
      .h1 {
        @include responsiveFont (30,50,320,1550);
        padding-bottom: 10px;
      }
      .box {
        border-radius: 15px;
        padding:#{$padding} 15px;
        background-color:$yellow;
        margin:#{$margin} auto 0 auto;
        font-size:14px;
        li {
          max-width:320px;
          margin:auto;
          padding:15px 0;
          border-bottom:1px dashed #{$mainColor};
          position:relative;
          &:first-of-type {
            padding-top:0;
          }
          &:before {
            content:'';
            position:absolute;
            bottom:-8px;
            left:-8px;
            background:url('../imgs/icon-bg1.png') center no-repeat;
            background-size:contain;
            width:30px;
            height:30px;
          }
          i {
            color:$mainColor;
            margin-right:$margin / 2;
            text-align:center;
            font-size:12px;
          }
        }
      }
    }
  }
  
}

@keyframes smoke {
  0%   {transform: translateY(0px) translateX(0) scale(0);opacity:1}
  100% {transform: translateY(-150px) translateX(-100px) scale(1);opacity:0}
}

// responsive
@media (max-width:991px) {
  .home {
    // header
    .header {
      max-height: 100%;
      height:auto;
      padding-top:$padding;
      .row {
        max-height: 100%;
        height:auto;
        h1 {
          transform: none;
          padding-left:25px;
        }
        img {
          max-width:250px;
          margin-bottom:$margin / 2;
        }
      }
    }

    // about
    .about {
      .txt {
        padding:15px 0;
      }
      .img {
        max-width:350px;
        margin:auto;
      }
    }

    // menu
    .menu .layout {
      .h1 {
        margin-bottom:$margin
      }
      .row {
        margin-top:$margin / 2;
      }
      .img {
        max-width:150px;
        margin:auto;
      }
    }

    // contact
    .contact {
      padding-top:$padding;
      .h1 {
        padding-bottom:10px;
      }
      .img {
        max-width:150px;
        margin:10px auto #{$margin / 2} auto;
        &:before, &:after {
          top: 55px;
          width: 15px;
          height: 30px;
          background-color:$mainColor;
        }
      }
      form {
        padding-top:0;
        input[type='mail'],textarea {
          margin-top:$margin / 4
        }
      }
    }

    // location
    .location {
      .img {
        max-width:150px;
        margin:$margin / 2 auto 0 auto;
      }
      .info {
        position:static;
        transform: none;
        .box {
          margin-top:$margin / 2
        }
      }
    }
  }
}

@media (max-width:991px) {
  @media (min-width:768px) {
    // feats
    .feats .row {
      position:relative;
      &:before,&:after {
        content: '';
        position: absolute;
      }
      &:before {
        left: 50%;
        top: 0;
        border-right: 1px dashed#{$mainColor};
        width: 1px;
        height: 100%;
      }
      &:after {
        top: 50%;
        left: 0%;
        border-bottom: 1px dashed#{$mainColor};
        height: 1px;
        width: 100%;
      }
    }
  }

  @media (max-width:767px) {
    .feats .row .col-lg-3:not(:last-of-type) .box {
      border-bottom: 1px dashed#{$mainColor};
    }
  }
}