// map
.map iframe {
  width:100%;
  height:350px;
}

.footer {
  min-height:200px;
  background:#{darken($yellow, 0%)} url("../imgs/footer3.png") center no-repeat;
  background-size:contain;
  overflow:hidden;
  .txt {
    color:$mainColor;
    letter-spacing:5px;
    width:100px;
    font-size:14px;
    margin-bottom:35px;
    margin-top:30px;
    &:last-of-type {
      margin-bottom:30px;
      margin-top:0px;
    }
  }
  p:not(.txt) {
    display:block;
    span {
      font-family:$specialFont;
    }
  }
}