// menu
.menu {
  background-color:$sub2Color;
  padding:#{$padding} 0;
  .h1 {
    @include responsiveFont (50,100,320,1550);
  }

  .layout {
    max-width:991px;
    margin:0 auto;
    background-color:$subColor;
    border:60px solid transparent;
    border-image:url('../imgs/menu5.png') 120 round;

    .txt {
      position:relative;
      top:50%;
      transform: translateY(-50%);
      max-width:280px;
      margin:auto;
      border-bottom:1px dashed #{$mainColor};
      padding-bottom:$padding / 3;
      span {
        display:inline-block;
        background:url('../imgs/bg.png') center no-repeat;
        background-size:contain;
        width:100px;
        height:40px;
        line-height:40px;
        text-align:center;
      }
    }

    .img {
      img {
        margin:auto;
        display:block;
      }
      transition:all 1s ease;
      &:hover {
        transform:rotate(45deg);
      }
    }

    .see-more {
      color:$txt;
      margin-top:30px
    }
  }
}

@media (max-width: 567px) {
  .menu .layout .img img {
    max-width:100px;
  }
  .menu .layout .txt {
    font-size:14px;
  }
}